import React from 'react';
import ReactDOM from 'react-dom';
import {HashRouter, Route} from 'react-router-dom';
import Button from './Button';
import DropdownBase from './Dropdown/ref';
import Dropdown from './Dropdown/dropdown';
import Dropdown2 from './Dropdown/dropdown2';
import Checkbox from './Checkbox';
import Dialog from './Dialog';
import Multichoice from './Multichoice';
import Notification from './Notification'
import RadioButton from './RadioButton'
import ReadonlyTable from './ReadonlyTable'
import Spinner from './Spinner'
import Text from './Text'
import TextArea from './TextArea'
import DropdownDemo from './Wrap/index';
import MobileCheckBox from './MobileCheckBox'
import '@webcomponents/webcomponentsjs/webcomponents-bundle.js'

ReactDOM.render(
  <HashRouter basename="/">
    <Route exact path="/Button" component={Button} />
    <Route exact path="/Dropdown/ref" component={DropdownBase} />
    <Route exact path="/Dropdown/dropdown" component={Dropdown} />
    <Route exact path="/Dropdown/dropdown2" component={Dropdown2} />
    <Route exact path="/Checkbox" component={Checkbox} />
    <Route exact path="/Dialog" component={Dialog} />
    <Route exact path="/Multichoice" component={Multichoice} />
    <Route exact path="/Notification" component={Notification} />
    <Route exact path="/RadioButton" component={RadioButton} />
    <Route exact path="/ReadonlyTable" component={ReadonlyTable} />
    <Route exact path="/Spinner" component={Spinner} />
    <Route exact path="/Text" component={Text} />
    <Route exact path="/TextArea" component={TextArea} />
    <Route exact path="/demo" component={DropdownDemo} />
    <Route exact path="/mobileCheckBox" component={MobileCheckBox} />

    {/* <Route exact path="/demo2" component={} /> */}

  </HashRouter>,
  document.getElementById('root')
);
