import React, {useEffect, useRef} from 'react';

const Notification = () => {
  const ref = useRef<HTMLDivElement>(null);
  let currentRef: any;
  useEffect(() => {
    currentRef = ref.current;
    if (currentRef) {
      currentRef.text = '不正です!!';
      currentRef.type = 'info';
    }
  }, []);
  return (
    <div>
      <kuc-notification ref={ref}> </kuc-notification>
      <button onClick={() => currentRef.open()}>open</button>
      <button onClick={() => currentRef.close()}>close</button>
    </div>
  );
};

export default Notification;
