import React, {useEffect, useRef} from 'react';

const DropdownBase = () => {
  const inputEl = useRef<HTMLDivElement>(null);
  let currentRef: any;

  useEffect(() => {
    currentRef = inputEl.current;
    if (currentRef) {
      currentRef.items = [
        {label: '-----', value: '-----'},
        {label: 'Orange', value: 'orange'},
        {label: 'Apple', value: 'apple'},
      ];
      currentRef.value = '-----';
      currentRef.label = 'フルーツ一覧';
      currentRef.requiredIcon = true;
      currentRef.error = 'エラーです';
      currentRef.visible = true;
      currentRef.onchange = (event: any) => console.log(event);
    }
  }, []);
  return (
    <div>
      <kuc-dropdown className={"hehe"} ref={inputEl}></kuc-dropdown>
    </div>
  );
};

export default DropdownBase;
