import React, { useState, useRef, useEffect, Fragment } from 'react';

function KucDropdownReact(props:any) {
  const ref = useRef<HTMLElement>(null);
  let currentRef: any;

  let [internalProps, setInternalProps] = useState<any>({});

  useEffect(() => {
    currentRef = ref.current;

    if (!currentRef) return;

    Object.keys(internalProps).forEach(name => {
      if (name.substring(0, 2) === 'on') {
        currentRef[name.toLowerCase()] = internalProps[name];
      }
    });
  }, [ref, internalProps]);

  useEffect(() => {
    console.log("useEffect",props);
    const tempProps:any = {};
    Object.keys(props).forEach(name => {
      if(typeof props[name] === 'object') {
        tempProps[name] = JSON.stringify(props[name]);
        return;
      }
      tempProps[name] = props[name];
    });
    setInternalProps(tempProps);
  }, [props]);

  return (
    <kuc-dropdown ref={ref} {...internalProps}/>
  );
}

function WrapContainer() {
  const [items, setItems] = useState([{label: 'option 1', value: 'option-1'}, {label: 'option 2', value: 'option-2'}]);
  const [value, setValue] = useState('option-1');

  function handleChangeItems() {
    setItems([{label: 'item 1', value: 'item-1'}, {label: 'item 2', value: 'item-2'}]);
  }

  function handleChangeValue() {
    setValue('option-2');
  }

  return (
    <Fragment>
      <KucDropdownReact
        items={items}
        onChange={(event: any) => console.log(event)}
        value={value}
      />
      <button onClick={handleChangeItems}>change items</button>
      <button onClick={handleChangeValue}>change value</button>
    </Fragment>
  );
}

export default WrapContainer;
