import React, {useRef, useEffect} from 'react';

const MobileCheckBox = () => {
  const ref = useRef<HTMLDivElement>(null);
  let currentRef: any;
  useEffect(() => {
    currentRef = ref.current;
    if (currentRef) {
      currentRef.id = 'base-1';
      currentRef.requiredIcon = true;
      currentRef.error = 'エラーです';
      currentRef.className = 'sample-className';
      currentRef.borderVisible = true;
      currentRef.itemLayout = 'vertical';
      currentRef.label = 'フルーツ';
      currentRef.disabled = true;
      currentRef.onchange = (event: CustomEvent) => {
        console.log(['detail.oldValue', event.detail.oldValue]);
        console.log(['detail.value', event.detail.value]);
      };
      currentRef.value = ['sample1'];
      currentRef.items = [
        {label: 'sample1', value: 'sample1'},
        {label: 'sample2', value: 'sample2'},
      ];
    }
  }, []);
  return (
    <div>
      <kuc-mobile-checkbox ref={ref}></kuc-mobile-checkbox>
    </div>
  );
};

export default MobileCheckBox;
