import React, {useState, useRef, useEffect, Fragment, ChangeEvent} from 'react';
interface DropDownItem {
  label: string;
  value: string;
}
interface DropdownReactProps {
  items: DropDownItem[] | string;
  value: string;
  label: string;
  requiredIcon: boolean;
  error: string;
  visible: boolean;
  onchange?: (event: ChangeEvent) => void;
}

function DropdownReact(props: DropdownReactProps) {
  const ref = useRef<DropdownReactProps>(null);
  let [items, setItems] = useState<string>();

  useEffect(() => {
    if (props.items && props.items.length > 0) {
      setItems(JSON.stringify(props.items));
    }
  }, [props.items]);

  useEffect(() => {
    if (ref.current) {
      ref.current.onchange = props.onchange;
    }
  }, [ref, props.onchange]);

  return (
    <kuc-dropdown
      ref={ref}
      error={props.error}
      visible={props.visible}
      value={props.value}
      label={props.label}
      items={items}
      requiredIcon={props.requiredIcon}
    />
  );
}

function App() {
  const [dropdown, setDropDown] = useState({
    items: [
      {label: '-----', value: '-----'},
      {label: 'Orange', value: 'orange'},
      {label: 'Apple', value: 'apple'},
    ],
    value: '-----',
    label: 'フルーツ一覧',
    requiredIcon: true,
    error: 'エラーです',
    visible: true,
    onchange: (event: ChangeEvent) => {
      console.log(event);
    },
  });
  function handleChangeItems() {
    dropdown.items = [
      {label: '-----', value: '-----'},
      {label: 'Apple', value: 'apple'},
    ];
    setDropDown(Object.assign({}, dropdown));
  }

  function handleChangeValue() {
    dropdown.value = 'apple';
    setDropDown(Object.assign({}, dropdown));
  }
  console.log('dropdown', dropdown);

  return (
    <Fragment>
      <DropdownReact {...dropdown} />
      <button onClick={handleChangeItems}>change items</button>
      <button onClick={handleChangeValue}>change value</button>
    </Fragment>
  );
}

export default App;
