import React, {useRef, useEffect} from 'react';

const ReadonlyTable = () => {
  const ref = useRef<HTMLDivElement>(null);
  let currentRef: any;
  useEffect(() => {
    currentRef = ref.current;
    if (currentRef) {
      currentRef.className = 'sample-className';
      currentRef.id = 'sample-id';
      currentRef.visible = true;
      currentRef.label = 'フルーツ';
      currentRef.columns = [
        {
          header: {
            text: '1st column',
          },
        },
        {
          header: {
            text: '2nd column',
          },
          visible: false,
        },
        {
          header: {
            text: '3rd column',
          },
        },
      ];
      currentRef.data = [
        ['', 'Orange', 'sample data 1'],
        ['', 'Lemon', 'sample data 2'],
        ['', 'Banana', 'sample data 3'],
      ];
    }
  }, []);
  return (
    <div>
      <kuc-readonly-table ref={ref}></kuc-readonly-table>
    </div>
  );
};

export default ReadonlyTable;
