import React, {useRef, useEffect} from 'react';

const Spinner = () => {
  const ref = useRef<HTMLDivElement>(null);
  let currentRef: any;
  useEffect(() => {
    currentRef = ref.current;
    if (currentRef) {
      currentRef.text = 'now loading...';
    }
  }, []);
  return (
    <div>
      <kuc-spinner ref={ref}></kuc-spinner>
    </div>
  );
};

export default Spinner;
