import React, {useRef, useEffect} from 'react';
import '@kintone/ui-component/lib/textarea';

const TextArea = () => {
  const ref = useRef<HTMLDivElement>(null);
  let currentRef: any;
  useEffect(() => {
    currentRef = ref.current;
    if (currentRef) {
      currentRef.onchange = (event: CustomEvent) => {
        console.log(event);
        console.log(event.detail.oldValue);
        console.log(event.detail.value);
      };
      currentRef.onfocus = (event: CustomEvent) => {
        console.log(event);
        console.log(event.detail.value);
      };
    }
  }, []);
  return (
    <div>
      <kuc-textarea
        ref={ref}
        label={'フルーツ'}
        requiredIcon={true}
        value={'Apple'}
        error={'エラーです'}
        className={'options-className'}
        id={'options-id'}
        visible={true}
      ></kuc-textarea>
    </div>
  );
};

export default TextArea;
