import React, {useEffect, useRef} from 'react';

const Button = () => {
  const ref = useRef<HTMLDivElement>(null);
  let currentRef: any;
  useEffect(() => {
    currentRef = ref.current;
    if (currentRef) {
      currentRef.id = 'normal-button';
      currentRef.onclick = "console.log('normalClick',event)";
      currentRef.className = 'normal-button';
      currentRef.text = 'normal-button';
    }
  }, []);
  return (
    <div>
      <kuc-button ref={ref}></kuc-button>
    </div>
  );
};

export default Button;
