import React, {useRef, useEffect, ChangeEvent} from 'react';

const Multichoice = () => {
  const ref = useRef<HTMLDivElement>(null);
  let currentRef: any;
  useEffect(() => {
    currentRef = ref.current;
    if (currentRef) {
      currentRef.label = 'Mutiple-Choice';
      currentRef.requiredIcon = true;
      currentRef.items = [
        {
          label: 'Item 1',
          value: 'item-1',
        },
        {
          label: 'Item 2',
          value: 'item-2',
        },
        {
          label: 'Item 3',
          value: 'item-3',
        },
        {
          label: 'Item 4',
          value: 'item-4',
        },
        {
          label: 'Item 5',
          value: 'item-5',
        },
      ];
      currentRef.value = ["item-1", "item-3"];
      currentRef.error = 'Error occurred!';
      currentRef.className = 'sample-className';
      currentRef.id = 'sample-id';
      currentRef.onchange = (event: ChangeEvent) => console.log(event);
    }
  }, []);
  return (
    <div>
      <kuc-multi-choice ref={ref}></kuc-multi-choice>
    </div>
  );
};

export default Multichoice;
