import React, {useEffect, useRef} from 'react';
import '@kintone/ui-component/lib/radio-button';
const RadioButton = () => {
  const ref = useRef<HTMLDivElement>(null);
  let currentRef: any;
  useEffect(() => {
    currentRef = ref.current;
    if (currentRef) {
      currentRef.onchange = (event: CustomEvent) => console.log(event.detail);
    }
  }, []);
  return (
    <div>
      <kuc-radio-button
        items={JSON.stringify([
          {
            label: 'Item 1',
            value: 'item-1',
          },
          {
            label: 'Item 2',
            value: 'item-2',
          },
          {
            label: 'Item 3',
            value: 'item-3',
          },
        ])}
        value={'item-2'}
        className={'sample-className'}
        id={'sample-id'}
        visible={true}
        borderVisible={true}
        itemLayout={'vertical'}
        label={'Radio button'}
        requiredIcon={true}
        error={'Error occurred!'}
        ref={ref}
      ></kuc-radio-button>
    </div>
  );
};

export default RadioButton;
